import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
// import Footer from './cmp/fancyfooter/footer';
import Footer from './cmp/footer/footer';
import Result from './cmp/result/result';
import SearchBox from './cmp/search/box';
import './custom.css';
import DataContext from './ctx/data.ctx';
import AppReducer from './ctx/app.reducer';
import DispatchContext from './ctx/dispatch.ctx';
import data from './mockdata';

const meta = { size: 8, page: 1, total: 10 };

function App() {
  const [state, dispatch] = React.useReducer(AppReducer, { meta, term: '', data });
  return (
  <DispatchContext.Provider value={dispatch}>

        <nav className="navbar navbar-dark bg-info">
          <h3 className="navbar-brand" href="#">Software</h3>
        </nav>

        <div className="container">
          <div className="row">
            <SearchBox />
          </div>

        <DataContext.Provider value={state}>
          <div className="row">
            <div className="col-12">
              <Result />
            </div>
          </div>
        </DataContext.Provider>

        </div>
        <br />
        <br />
        <br />
        <Footer />
  </DispatchContext.Provider>
  );
}

export default App;
