import React from 'react';
import useNav from './nav.hook';

const ResultNav = () => {
  const {
    pages, getNewPage, page, size, totalPages,
  } = useNav();
  return (
        <nav className="d-flex justify-content-center">
      <ul className="pagination pagination-base pagination-boxed pagination-square mb-0">
        {page > size
          && <li className="page-item">
            <a className="page-link no-border" onClick={() => { getNewPage(page - 3); }} href={`#page=${page - 3}`}>
              <span aria-hidden="true">Â«</span>
              <span className="sr-only">Previous</span>
            </a>
          </li>
        }

        {/* the page numbers */}
        {pages.map(i => ++i === page
          ? (<li className="page-item active" key={i}><a className="page-link no-border" href={`#page=${i}`}>{ i }</a></li>)
          : (<li className="page-item" key={i}><a className="page-link no-border" onClick={() => { getNewPage(i); }} href={`#page=${i}`}>{ i }</a></li>))
        }
        {/* the page numbers */}
        
        {page + size < totalPages
          && <li className="page-item">
            <a className="page-link no-border" onClick={() => { getNewPage(page + 3); }} href={`#page=${page + 3}`}>
                <span aria-hidden="true">Â»</span>
                <span className="sr-only">Next</span>
            </a>
        </li>
        }
    </ul>
</nav>
  );
};
export default ResultNav;
