const baseUrl = 'http://localhost:5000';
const requestInit = {
  method: 'POST',
  // mode: 'no-cors', // no-cors, *cors, same-origin
  cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
  // credentials: 'same-origin', // include, *same-origin, omit
  headers: {
    'Content-Type': 'application/json',
    // 'Content-Type': 'application/x-www-form-urlencoded',
  },
  redirect: 'follow', // manual, *follow, error
  referrerPolicy: 'no-referrer',
};

export const RequestSearch = async ({ term, page }) => {
  
  const requestBody = JSON.stringify({ term });
  const url = `${baseUrl}/api/search/all?page=${page}`;
  console.log('url : ',url)
  const options = {
    ...requestInit,
    body: requestBody,
  };

  return fetch(url, options)
    .then((r) => r.json())
    // .then(({ data, meta }) => ({ data, meta }))
    .catch((err) => {
      // eslint-disable-next-line no-console
      console.error('request error, URL: ', url, 'options: ', options, 'error: ', err);
    });
};

export const RequestEmpty = async ({ term, page }) => { };
