import React from 'react';

const Row = ({
  url, title, abstract, authors, rank, citation, year, tags
}) => (
<tr>
  <td>
    <div className="widget-26-job-emp-img">
      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-journal-text" viewBox="0 0 16 16">
        <path d="M5 10.5a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5zm0-2a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5zm0-2a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5zm0-2a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5z"/>
        <path d="M3 0h10a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2v-1h1v1a1 1 0 0 0 1 1h10a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H3a1 1 0 0 0-1 1v1H1V2a2 2 0 0 1 2-2z"/>
        <path d="M1 5v-.5a.5.5 0 0 1 1 0V5h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1zm0 3v-.5a.5.5 0 0 1 1 0V8h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1zm0 3v-.5a.5.5 0 0 1 1 0v.5h.5a.5.5 0 0 1 0 1h-2a.5.5 0 0 1 0-1H1z"/>
      </svg>
    </div>
  </td>
  <td className="col-md-5">
    <div className="widget-26-job-title">
        <a href={url} target="blank" className="text-info">{ title }</a>
        <span className="text-muted time"> {year} </span>
        <p className="m-0 paragraph-text">
          {abstract && abstract.substring(0, 200)}...
        </p>
    </div>
  </td>
  <td className="mid">
    <div className="widget-26-job-info">
        <p className="text-muted m-0"><span>{authors && authors.substring(0, 100)}</span></p>
    </div>
  </td>
  <td className="mid">
    <div className="type m-0">rank: {parseFloat(rank || 0).toFixed(1)}</div>
    <p className="type m-0">cite: {citation || 0} </p>
  </td>
    <td className="mid">
      {tags && tags.map && tags.map((t, i) => (
        <span className="badge badge-info" key={i}> {t} </span>
      ))}
  </td>
</tr>
);

export default Row;
