import React, { useContext , useMemo } from 'react';
import DataContext from '../../ctx/data.ctx';
import DispatchContext from '../../ctx/dispatch.ctx';
import { RequestSearch } from '../../req/req';

const useNav = () => {
  const dispatch = useContext(DispatchContext);
  const { meta , data } = useContext(DataContext);
  const { size, page, total } = meta;
  const totalPages = useMemo(()=> Math.ceil(total / size) , [total,size]);
  console.log('nav.hook : ',meta , data, totalPages)
  
  const { term } = useContext(DataContext);
  const getNewPage = (pageId) => {
    // const newpage = event.target.innerText;
    console.log('term : ',term ,'meta : ',meta ,'data : ',data,'page : ',page)
    RequestSearch({ term, page: pageId })
      .then((result) => {
        const action = { term };
        // eslint-disable-next-line no-underscore-dangle
        if (result.data) action.data = result.data.map((d) => d._source);
        if (result.currentPage) action.meta = {
          total: parseInt(result.totalArticles),
          page: parseInt(result.currentPage),
          size: parseInt(result.pageSize),
        };
        dispatch(action);
      })
      .catch((error) => {
        // eslint-disable-next-line no-console
        console.error(error);
      });
  };
  // eslint-disable-next-line
  const pages =  useMemo(() => Array(totalPages)
    .fill(1)
    .map((_v, i) => i)
    .filter((i) => page - 4 < i && i < page + 2),
    [page, totalPages]);
  
  return {
    pages,
    page,
    totalPages,
    size,
    getNewPage,
  };
  console.log('here')
};

export default useNav;
