import { useEffect, useContext, useState } from 'react';
import DispatchContext from '../../ctx/dispatch.ctx';
import { RequestSearch } from '../../req/req';

const useSearch = () => {
  const dispatch = useContext(DispatchContext);
  const [term, setTerm] = useState('');
  const onType = (event) => {
    const { value } = event.target;
    setTerm(value);
  };

  const onClick = (event) => {
    event.preventDefault();

    RequestSearch({ term , page:1 })
      .then(({ data, meta,
        totalArticles,
        pageCount,
        currentPage,
        pageSize,
      }) => {
      
        const action = { term };
        // eslint-disable-next-line no-underscore-dangle
        if (data) action.data = data.map((d) => d._source);
        //if (data) action.data = data;
        if (currentPage) action.meta = {
          total:parseInt( totalArticles),
          page: parseInt(currentPage),
          size: parseInt(pageSize),
        };
        dispatch(action);
      })
      .catch((error) => {
        // eslint-disable-next-line no-console
        console.error(error);
      });
  };
  useEffect(() => {
    //-
  }, []);
  return { onClick, onType, term };
};

export default useSearch;
