import React from 'react';
import useSearch from './box.hook';

const SearchBox = () => {
  const { onClick, onType, term } = useSearch();
  return (
    <div className="col-lg-12 card-margin">
      <div className="card search-form">
        <div className="card-body p-0">
          <form id="search-form">
            <div className="row">
              <div className="col-12">
                <div className="row no-gutters">
                  <div className="col-lg-10 col-md-10 col-sm-10 p-0">
                    <input type="text" placeholder="Search..." className="form-control" id="search" name="search" value={term} onChange={onType} />
                  </div>
                  <div className="col-lg-2 col-md-2 col-sm-2 p-0">
                    <button type="submit" className="btn btn-base" onClick={onClick}>
                      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-search"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line></svg>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default SearchBox;
